import React from "react";
import auth from "./auth";

// This is the actual page that is the startboy.

export function AppLayout(props) {
  return (
    <div>
      <h3>This is the Main Page that is Guarded.</h3>
      <button
        onClick={() => {
          auth.logout(() => {
            props.history.push("/");
          });
        }}
      >
        Log out
      </button>
    </div>
  );
}
