import React from "react";
import auth from "./auth";

export function LandingPage(props) {
  return (
    <div>
      <h3>This is the landing page.</h3>
      <button
        onClick={() => {
          auth.login(() => {
            props.history.push("/app");
          });
        }}
      >
        Login
      </button>
    </div>
  );
}
